    position: fixed;
    top: 0;
    transition: transform 0.3s cubic-bezier(0.33, 0.65, 0.81, 0.7);
    width: 100vw; }
    .mobile-nav nav.menu-closed {
      transform: translateX(-100%); }
    .mobile-nav nav .close-menu {
      text-align: right; }
      .mobile-nav nav .close-menu button {
        color: white;
        font-size: 3rem;
        background: transparent;
        padding: 7px;
        border: none; }
    .mobile-nav nav a {
      color: white;
      display: block;
      margin: 0 20px;
      font-size: 1.5rem;
      line-height: 3; }
      .mobile-nav nav a + a {
        border-top: 1px solid white; }

